*
    box-sizing: border-box

img
    max-width: 100%

body
    padding: 0
    margin: 0

#root
    background-color: #d3d6e0
    min-height: 100vh

.container
    max-width: 1140px
    margin: auto
    padding: 0 15px

.topbar
    background-color: #302F32
    padding: 0 15px
    color: #fff
    .container
        display: flex
        flex-wrap: wrap
        align-items: center
        justify-content: space-between
    .logo
        padding: 15px 15px 15px 0
        width: 100px
        border-right: 1px solid #464547
    .info
        display: flex
        flex-direction: column
        padding: 15px
    .full-version
        font-family: Bold
        color: #fff
    .nav
        a
            color: #fff
            text-decoration: none
            padding: 10px
            &:hover
                text-decoration: underline
    .user
        display: flex
        align-items: center
    .user__avatar
        width: 51px
        height: 51px
        border-radius: 50%
        margin-right: 10px
        overflow: hidden
    .user__info
        b
            font-size: 16px
            font-family: Bold
        span
            font-size: 14px

.section
    background-color: #E1E3E7
    &__header
        background-color: #3B3A40
        padding: 15px 20px
        color: #fff
        font-weight: 700
        font-size: 20px
        font-family: Bold
        border-radius: 14px 14px 0 0
        display: flex
        align-items: center
        .icon
            margin-right: 15px
        .collaps-icon
            margin-left: auto
            cursor: pointer
            opacity: 0.7
            transition: all .3s
            &:hover
                opacity: 1
    &__body
        padding: 24px

.field
    margin-bottom: 22px
    display: block
    .header
        display: flex
        margin-bottom: 10px
        font-weight: 600
        .length
            margin-left: auto
            img
                margin-left: 10px
    .input
        padding: 17px 20px
        font-size: 15px
        border: 0
        border-radius: 8px
        box-sizing: border-box
        width: 100%

@media (max-width: 540px)
    .topbar
        .container
            .user
                margin: 15px auto
